import React, { Component } from "react";
import Chart from "react-apexcharts";
import ApiCall from "../../../Services/Function/ApiCall";
//import {
  //setClientName,
  //setHistoryData,
//} from '../../../../reducers/ThemeOptions';
import {connect} from 'react-redux';
var graph_array={};
class Mixed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      flag:false,
      graph_array:{},
      optionsMixedChart1: {
        tooltip: {
          enabled: true,
          enabledOnSeries: undefined,
          shared: true,
          followCursor: false,
          y: {
            formatter: function(value ,{series, seriesIndex, dataPointIndex, w }){
              return (graph_array[value])
            }
          }
        },
        chart: {
          height: 350,
          type: "line",
          toolbar: {
            show: false,
          },
        },
        stroke: {
          width: [0, 4],
        },
        xaxis: {
          type: "category",
          tickPlacement: 'between',
          tooltip: {
            enabled: false,
            formatter: function(val, opts) {
              return val
            }
          },
          labels: {
            show: true,
            rotate: -90,
            rotateAlways: false,
            hideOverlappingLabels: true,
            showDuplicates: false,
            trim: false,
            minHeight: undefined,
            maxHeight: 120,
            style: {
                colors: [],
                fontSize: '12px',
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontWeight: 400,
                cssClass: 'apexcharts-xaxis-label',
            },
            offsetX: 0,
            offsetY: 0,
            format: undefined,
            formatter: undefined,
            datetimeUTC: true,
            datetimeFormatter: {
                year: 'yyyy',
                month: "MMM 'yy",
                day: 'dd MMM',
                hour: 'HH:mm',
            },
          }
        },
        yaxis: [
          {
            title: {
              // text: "Long Term Rating",
            },
            show: true,
            showAlways: true,
            tickAmount: 4,
            max: 19,
            min: 15,
            labels: {
                show: true,
                align: 'right',
                minWidth: 0,
                maxWidth: 160,
                formatter: (value, opts) => {
                  return (graph_array[ value.toFixed(0)]);
                },
            },
          },
        ],

      },
      seriesMixedChart1: [
        {
          name: "Rating",
          type: "column",
          data: [440, 505, 414, 671, 227, 413, 201, 352, 752, 320, 257, 160],
        },
        {
          name: "Rating",
          type: "line",
          // data: [440, 505, 414, 671, 227, 413, 201, 352, 752, 320, 257, 160],
          
        },
      ],
    };
  }
  HandleSetData=async()=>
  {
    let graph_data=[];
    let graph_date=[];
    Object.keys(this.props.historyData).map((val, i) => {
      if(this.props.historyData[i]['lterm'])
      {
        graph_array[parseInt(this.props.historyData[i]['Intensity'])]=this.props.historyData[i]['lterm'];
        graph_data.push(parseInt(this.props.historyData[i]['Intensity']));
      }
      if(this.props.historyData[i]['notification_date'])
      {
        let date=new Intl.DateTimeFormat('en-GB', { 
        year: "2-digit",
        day: "2-digit",
        month: "short"
        }).format((new Date(this.props.historyData[i]['notification_date'])).getTime());
        graph_date.push(date);
      }
    })
    let temp=this.state.seriesMixedChart1;
    temp[0]['data']=graph_data;
    this.HandleSetState('seriesMixedChart1',temp);
    this.HandleSetState('graph_array',graph_array);
    await this.HandleSetOptionData(graph_date);
    this.HandleSetState('flag',true);
  }
  HandleSetOptionData=(graph_date)=>
  {
    var min = Math.min.apply( null,Object.keys(graph_array));
    let tickAmount=Object.keys(graph_array).length;
    if(tickAmount == 2)
    {
      min=min-2;
      tickAmount=tickAmount+2;
    }
    let temp_date=this.state.optionsMixedChart1;
    if(tickAmount != 1 && tickAmount != 3)
    {
      temp_date.yaxis[0].tickAmount=tickAmount;
      temp_date.yaxis[0].min=min;
    }
    temp_date['labels']=graph_date;
    this.HandleSetState('optionsMixedChart1',temp_date);

  }
  async componentWillMount() {
    // var history_data=this.props.historyData;
    // if (Object.keys(history_data).length == 0)
    // {
    //   let data={
    //     'id':JSON.parse(localStorage.getItem('user'))['opinion_id']
    //   }
    //   history_data=await ApiCall.postAPICall(1,'get/opinion/history',data);
    //   history_data=history_data['data'];
    //   let {setHistoryData } = this.props;
    //   await setHistoryData(history_data);
    // }
    // let {setClientName } = this.props;
    // await setClientName(history_data[0]?history_data[0]['Entity']:'Menu');
    // await localStorage.setItem('ClientName', JSON.stringify(history_data[0]?history_data[0]['Entity']:'Menu'));
    // console.log(JSON.parse(localStorage.getItem('ClientName')));
    // this.HandleSetData();
  }
  HandleSetState=(key,value)=>
  {
     this.setState({
      [key]: value
    });
  }
  render() {
    return (
      <div className="bar">
        {this.state.flag ?
          <Chart options={this.state.optionsMixedChart1} series={this.state.seriesMixedChart1}
            type="line" width="100%" height="325px"/>
          :null
        }
      </div>
    );
  }
}
const mapStateToProps = state => ({
 // clientName:state.ThemeOptions.clientName,
  //historyData:state.ThemeOptions.historyData,
});

const mapDispatchToProps = dispatch => ({
  //setClientName:enable=>dispatch(setClientName(enable)),
  //setHistoryData:enable=>dispatch(setHistoryData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Mixed);
