import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownMenu,
  DropdownItem,
  Container,
  CardTitle,
  Collapse,
  ListGroup,
  ListGroupItem
} from "reactstrap";
import ApiCall from "../../../../Services/Function/ApiCall";
import Loading from '../../../../Loading';

import {VerticalTimeline, VerticalTimelineElement} from 'react-vertical-timeline-component';

import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

import {
    faCoffee,
    faArchive,
    faCheck,
} from '@fortawesome/free-solid-svg-icons';

import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  setClientName
} from '../../../../../reducers/ThemeOptions';
import {connect} from 'react-redux';
class index extends Component {
  HandleProgress=(key)=>
  {
    return  this.props.data['progress'][key] ?true:false;
  }
  render() {
    return (
      <Fragment>
        { this.props.data['progress'] ?
          <Card className="main-card">
            <CardBody>
              <PerfectScrollbar>
                  <VerticalTimeline layout="1-column" className="vertical-time-simple vertical-without-time">
                    <VerticalTimelineElement className={"vertical-timeline-item" + (this.HandleProgress('info_upload_date')?' dot-success':'') }>
                        <h4 className="timeline-title">
                          Info
                          {this.HandleProgress('info_upload_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                        </h4>
                        <p>
                        </p>
                    </VerticalTimelineElement>
                    <VerticalTimelineElement className={"vertical-timeline-item" +(this.HandleProgress('assessment_upload_date')? ' dot-success':'') }>
                      <h4 className="timeline-title">
                        Assessments
                        {this.HandleProgress('assessment_upload_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                      </h4>
                    </VerticalTimelineElement>
                    <VerticalTimelineElement className={"vertical-timeline-item" +(this.HandleProgress('site_visit_date')? ' dot-success':'') }>
                      <h4 className="timeline-title">
                        Site Visit
                        {this.HandleProgress('site_visit_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                      </h4>
                    </VerticalTimelineElement>
                    <VerticalTimelineElement className={"vertical-timeline-item" +(this.HandleProgress('mm_upload_date')? ' dot-success':'') }>
                      <h4 className="timeline-title">
                        MM
                        {this.HandleProgress('mm_upload_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                      </h4>
                    </VerticalTimelineElement>
                    <VerticalTimelineElement className={"vertical-timeline-item" +(this.HandleProgress('rc_upload_date')? ' dot-success':'') }>
                      <h4 className="timeline-title">
                        RC
                        {this.HandleProgress('rc_upload_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                      </h4>
                    </VerticalTimelineElement>
                    <VerticalTimelineElement className={"vertical-timeline-item" +(this.HandleProgress('ppl_date')? ' dot-success':'') }>
                      <h4 className="timeline-title">
                        PPL
                        {this.HandleProgress('ppl_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                      </h4>
                    </VerticalTimelineElement>
                    <VerticalTimelineElement className={"vertical-timeline-item" +(this.HandleProgress('notification_date') ? ' dot-success':'' )}>
                      <h4 className="timeline-title">
                        Nl
                        {this.HandleProgress('notification_date') ?
                            <FontAwesomeIcon color="green" className="ml-2" icon={faCheck} />
                          :null}
                      </h4>
                    </VerticalTimelineElement>                    
                  </VerticalTimeline>
              </PerfectScrollbar>
            </CardBody>
          </Card>
          :null
        }   
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  clientName:state.ThemeOptions.clientName,
});

const mapDispatchToProps = dispatch => ({
  setClientName:enable=>dispatch(setClientName(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(index);
